<template>
  <div class="HistoryHeartRate">
    <sHeader name="历史数据"/>
    <van-tabs @click-tab="change">
      <van-tab title="日">
        <HistoryRecord type="day" :date="date" text="峰值" :option="option" :subtractDay="subtractDay" :addDay="addDay"/>
      </van-tab>
      <van-tab title="周">
        <HistoryRecord type="week" :start-week="startWeek" :end-week="endWeek" :option="option" :subtract-week="subtractWeek" :add-week="addWeek"/>
      </van-tab>
      <van-tab title="月">
        <HistoryRecord type="month" :start-month="startMonth" :end-month="endMonth" :option="option" :subtract-month="subtractMonth" :add-month="addMonth"/>
      </van-tab>
      <van-tab title="年">
        <HistoryRecord type="year" :start-year="startYear" :end-year="endYear" :option="option" :subtract-year="subtractYear" :add-year="addYear"/>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import sHeader from '@/components/SimpleHeader';
import {reactive, onMounted, toRefs, ref} from 'vue';
import {useStore} from "vuex";
import {useRouter} from 'vue-router';
import moment from "moment";
import record from './history/heartrate-record/index'

export default {
  name: "HistoryBloodSugar",
  components: {
    sHeader,
    HistoryRecord: record,

  },
  props: {},
  setup() {
    const store = useStore();
    const router = useRouter();
    const goTo = (r, query) => {
      router.push({path: r, query: query || {}})
    }
    const option = ref({
      title: {
        left: 'center',
        text: '心率',
        textStyle: {
          fontSize:14,
          fontWeight:'normal',
        }
      },

      xAxis: {
        type: 'category',
        data: ['2021-07-27', '2021-07-28','2021-08-05']
      },
      yAxis: {
        type: 'value'
      },
      series: [{
        data: [150, 230, 224],
        type: 'line'
      }]
    })
    const checked = ref(false);
    const state = reactive({
      index:0,
      startWeek: moment().subtract(6, 'days').format('YYYY-MM-DD'),
      endWeek: moment().format('YYYY-MM-DD'),
      startMonth: moment().subtract(1, 'months').format('YYYY-MM-DD'),
      endMonth: moment().format('YYYY-MM-DD'),
      startYear: moment().subtract(1, 'years').format('YYYY-MM-DD'), // 当前时间往前推一年的时间
      endYear: moment().format('YYYY-MM-DD'),
      date: moment().format('YYYY-MM-DD')
    })
    const subtractDay = () => {
      let $date = moment(state.date).subtract(1, 'days').format('YYYY-MM-DD');
      state.date = $date;
    }
    const change =(index)=>{
      state.index  = index;
    }
    const addDay = () => {
      let currentDate = moment().format('YYYY-MM-DD');
      if (state.date === currentDate) {
        return;
      }
      let $date = moment(state.date).add(1, 'days').format('YYYY-MM-DD');
      state.date = $date;
    }
    const subtract = (start, end, amount, unit) => {
      let $start = moment(state[start]).subtract(amount, unit).format('YYYY-MM-DD');
      let $end = moment(state[end]).subtract(amount, unit).format('YYYY-MM-DD');
      state[start] = $start;
      state[end] = $end;
    }
    const add = (start, end, amount, unit) => {
      let currentDate = moment().format('YYYY-MM-DD');
      if (state[end] === currentDate) {
        return;
      }
      let $start = moment(state[start]).add(amount, unit).format('YYYY-MM-DD');
      let $end = moment(state[end]).add(amount, unit).format('YYYY-MM-DD');
      state[start] = $start;
      state[end] = $end;
    }

    const subtractWeek = () => {
      subtract('startWeek', 'endWeek', 6, 'days');
    }
    const addWeek = () => {
      add('startWeek', 'endWeek', 6, 'days');
    }
    const subtractMonth = () => {
      subtract('startMonth', 'endMonth', 1, 'months')
    }
    const addMonth = () => {
      add('startMonth', 'endMonth', 1, 'months')
    }
    const subtractYear = () => {
      subtract('startYear', 'endYear', 1, 'years')
    }
    const addYear = () => {
      add('startYear', 'endYear', 1, 'years')
    }
    return {
      ...toRefs(state),
      subtractDay,
      addDay,
      addWeek,
      subtractWeek,
      addMonth,
      subtractMonth,
      subtractYear,
      addYear,
      option,
      change,
      checked
    }
  }
}
</script>

