<template>
  <v-chart class="chart" :option="option"/>
</template>

<script>
import {onMounted, ref} from "vue";

export default {
name: "index",
  props:{
    type: {
      default:'week',
      type: String
    },
    list: Array
  },
  setup(props){
    const option = ref({
      title: {
        left: 'center',
        text: '血糖值（mmol/L）',
        textStyle: {
          fontSize:14,
          fontWeight:'normal',
        }
      },

      xAxis: {
        type: 'category',
        // data: ['2021-07-27', '2021-07-28','2021-08-05']
        data:[]
      },
      yAxis: {
        type: 'value'
      },
      series: [{
        type: 'line',
        data:[]
        // data: [150, 230, 224],

      }]
    })
    onMounted(()=>{
      const { list, type } = props
      console.log("图形分析",props)
      for (let i = 0; i < list.length; i++) {
        option.value.xAxis.data.push(type === "day" ? list[i].lineDate :  type === "year" ? list[i].yearDate : list[i].date )
        option.value.series[0].data.push( type === "day" ? list[i]['bloodSugar'] : list[i]['bloodSugarAverage'])
      }
      console.log( option.value.series)
    })
    return {
      option
    }
  }
}
</script>

