<!--选择日期-->

<template>
    <div class="div_changedate_content">
        <div @click="left">
            <svg aria-hidden="true" class="svg_left">
                <use xlink:href="#icon-goback"></use>
            </svg>
        </div>

        <span class="span_date">
        {{type == 'day'?  moment(selectdate).format('YYYY年MM月DD日')
              : type == 'week'? moment(new Date(selectdate).setDate(new Date(selectdate).getDate() - 7) ).format('YYYY/MM/DD') +'-'+ moment(selectdate).format('YYYY/MM/DD')
              : type == 'month'? moment(new Date(selectdate).setMonth(new Date(selectdate).getMonth() - 1) ).format('YYYY/MM/DD') +'-'+ moment(selectdate).format('YYYY/MM/DD')
              : type == 'year'? moment(new Date(selectdate).setFullYear(new Date(selectdate).getFullYear() - 1) ).format('YYYY/MM/DD') +'-'+ moment(selectdate).format('YYYY/MM/DD')
              :''
            }}
    </span>

        <div @click="right">
            <svg aria-hidden="true" class="svg_left">
                <use xlink:href="#icon-Fill"></use>
            </svg>
        </div>
    </div>
</template>
<script>
  import {reactive, onMounted, toRefs,} from 'vue'
  import moment from 'moment'

  export default {
    name: "ChangeDate",
    props: {
      selectdate: { //数据源
        type: Date,
        default: new Date()
      },
      onchange: { //更新数据
        type: Function,
        default: () => {
        }
      },
      type: {   //种类区分日周月年
        type: String,
        default: 'day'
      },
    },

    components: {},
    setup(props) {
      onMounted(
        () => {
        }
      )
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({})

      const left = () => {
        let oldDate = new Date(props.selectdate)
        let newDate;
        if (props.type == 'week') {
          newDate = oldDate.setDate(oldDate.getDate() - 7)
        } else if (props.type == 'month') {
          newDate = oldDate.setMonth(oldDate.getMonth() - 1);
        } else if (props.type == 'year') {
          newDate = oldDate.setFullYear(oldDate.getFullYear() - 1);
        } else {
          newDate = oldDate.setDate(oldDate.getDate() - 1);
        }
        if (newDate.valueOf() < new Date().valueOf()) {
          props.onchange(newDate)
        }
      }
      const right = () => {
        let oldDate = new Date(props.selectdate)
        let newDate;
        if (props.type == 'week') {
          newDate = oldDate.setDate(oldDate.getDate() + 7)
        } else if (props.type == 'month') {
          newDate = oldDate.setMonth(oldDate.getMonth() + 1);
        } else if (props.type == 'year') {
          newDate = oldDate.setFullYear(oldDate.getFullYear() + 1);
        } else {
          newDate = oldDate.setDate(oldDate.getDate() + 1)
        }
        if (newDate.valueOf() < new Date().valueOf()) {
          props.onchange(newDate)
        }
      }

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        left,
        right,
        moment,
      }
    }
  }
</script>
<style scoped lang="less">
    .div_changedate_content {
        height: 44px;
        display: flex;
        justify-content: space-between;
        background-color: #fff;
        padding: 10px 10px 0 10px;
        align-items: center;
    }

    .svg_left {
        height: 17px;
        width: 9px;
        justify-content: center;
        align-items: center
    }

    .span_date {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #08B48E;
        letter-spacing: 0.18px;
    }
</style>
