<template>
  <div class="scllowBox" :style="{ backgroundColor: '#F5F6FE',display: 'flex', flexDirection: 'column', paddingBottom: '10px' }">

    <div class="margin_box" :style="{  marginTop: '10px',paddingTop: '5px' }">
      <!--选择日期-->
      <change-date :style="{ backgroundColor: '#fff'}" :selectdate="selectdate" :type="type" :onchange="onchange"/>
      <!---->
      <van-divider>空腹</van-divider>
      <div class="list-row-div">
        <div class="list-row-sugar">
          <p>{{ measurePeriodOneObj.max || '一一'}}</p>
          <p>峰值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodOneObj.min || '一一'}}</p>
          <p>谷值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodOneObj.average || '一一'}}</p>
          <p>均值</p>
        </div>
      </div>
      <van-divider>餐后2小时</van-divider>
      <div class="list-row-div">
        <div class="list-row-sugar">
          <p>{{ measurePeriodTwoObj.max || '一一'}}</p>
          <p>峰值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodTwoObj.min || '一一'}}</p>
          <p>谷值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodTwoObj.average || '一一'}}</p>
          <p>均值</p>
        </div>
      </div>
      <van-divider>餐前</van-divider>
      <div class="list-row-div">
        <div class="list-row-sugar">
          <p>{{ measurePeriodThreeObj.max || '一一'}}</p>
          <p>峰值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodThreeObj.min || '一一'}}</p>
          <p>谷值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodThreeObj.average || '一一'}}</p>
          <p>均值</p>
        </div>
      </div>
      <van-divider>凌晨</van-divider>
      <div class="list-row-div">
        <div class="list-row-sugar">
          <p>{{ measurePeriodFourObj.max || '一一'}}</p>
          <p>峰值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodFourObj.min || '一一'}}</p>
          <p>谷值</p>
        </div>
        <div class="list-row-sugar">
          <p>{{ measurePeriodFourObj.average || '一一'}}</p>
          <p>均值</p>
        </div>
      </div>
    </div>
    <!--本周、日、月、年数据-->
    <div class="d_h_r_item">
      <span class="span_drinkadd_title">{{ title }}</span>
      <label class="lable_item">图形分析:</label>
      <van-switch v-model="checked"  @update:model-value="onUpdateValue"  size="16px"  active-color="#15AC78" inactive-color="#dcdee0"/>
    </div>
    <!--  历史记录  -->
    <div class="margin_box scroll_box" :style="{ borderTopLeftRadius: 0, borderTopRightRadius: 0}"  >
      <div class="no_data_boold" v-show="list.length === 0">暂无数据</div>
      <HistoryRecord v-if="!checked && list.length !== 0" :list="list"  :type="type" :toYearDetial="toYearDetial"/>
      <LineECharts  :type="type" v-else-if="checked  && list.length !== 0" :list="lineList"/>
    </div>
  </div>

</template>

<script>
import moment from 'moment'
import  ChangeDate from '../../../components/ChangeDate'
import  HistoryRecord from '../heartrate-history-record/index'
import   LineECharts from '../heartrate-history-record/line-chart'
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { getListByStartEnd } from "@/service/bloodsuger";
import {formatYYMMDD, isuseDrug, measurePeriod} from "@/utils/util";
export default {
  name: "momtion-record",
  components: {
    ChangeDate,
    HistoryRecord,
    LineECharts
  },
  props:{
    type: {
      default:'week',
      type: String
    },
  },
  setup(props){
    const router = useRouter()

    onMounted(
        () => {
          // 赋值
          getList()
          state.title =props.type === "day"? "本日数据": props.type === "week" ? "本周数据" :  props.type === "month" ? "本月数据"  : "本年数据"
        }
    )
    // 获取数据
    const getList = async ()=>{
      const startTime =  getStartTime(props.type)
      const res = await getListByStartEnd(startTime,formatYYMMDD(state.selectdate))
      const { code, data} = res
      const  obj= data
      let mAfterlist = []
      let measurePeriodOneArray = [] //  清晨空腹
      let measurePeriodTwoArray = [] // 餐后2小时
      let measurePeriodThreeArray = [] //  清晨空腹
      let measurePeriodFourArray = [] // 餐后2小时
      Object.keys(obj).forEach((key)=>{
        const childList = []
        const arry = obj[key]
        let bloodSugarArray = []
        for (let i = 0; i < arry.length; i++) {
          // 清晨空腹
          arry[i].measurePeriod === 1 && measurePeriodOneArray.push(arry[i]['bloodSugar'])
          arry[i].measurePeriod === 2 && measurePeriodTwoArray.push(arry[i]['bloodSugar'])
          arry[i].measurePeriod === 3 && measurePeriodThreeArray.push(arry[i]['bloodSugar'])
          arry[i].measurePeriod === 4 && measurePeriodFourArray.push(arry[i]['bloodSugar'])
          bloodSugarArray.push(arry[i].bloodSugar)
          const itemObj = {
            yearDate:  moment(arry[i]['yYYYMMDD']).format("YYYY-MM"),
            date: props.type === "day" ? moment(arry[i]['measureTime']).format("hh:mm:ss"): moment(arry[i]['yYYYMMDD']).format("YYYY-MM-DD"),
            time: moment(arry[i]['measureTime']).format("hh:mm:ss"),
            lineDate: props.type === "day" ? moment(arry[i]['measureTime']).format("hh:mm:ss") :
                moment(arry[i]['yYYYMMDD']).format("YYYY-MM-DD") ,
            isuseDrugDic: isuseDrug[arry[i]['isuseDrug']],
            measureTimeDesc: arry[i].measureTimeDesc,
            measurePeriodDic: measurePeriod[arry[i]['measurePeriod']],
            bloodSugar: arry[i]['bloodSugar'],
            dataSourceDic: arry[i]['dataSource'] === 0 ? "手动记录" : "设备读取"
          }
          childList.push(itemObj)
          state.baseInfoList.push(itemObj)
        }
        mAfterlist.push({
          bloodSugarAverage: handleData(bloodSugarArray).average,
          measureTime: arry[0]['measureTime'],
          yearDate:  moment(arry[0]['measureTime']).format("YYYY-MM"),
          date: props.type === "day" ? moment(arry[0]['measureTime']).format("hh:mm:ss") : moment(arry[0]['measureTime']).format("YYYY-MM-DD"),
          child: childList
        })

      })
      state.measurePeriodOneObj = handleData(measurePeriodOneArray) || {}
      state.measurePeriodTwoObj = handleData(measurePeriodTwoArray) || {}
      state.measurePeriodThreeObj = handleData(measurePeriodThreeArray) || {}
      state.measurePeriodFourObj = handleData(measurePeriodFourArray) || {}
      if(props.type === "day"){
        state.list =  mAfterlist.length > 0 ? mAfterlist[0].child : []
      }else if(props.type !== "year"){
        state.list = mAfterlist
      }else{
        state.list = handleYearDatas(mAfterlist)
      }
      console.log("组装数据",mAfterlist, state.list)
      // 图形分析
      if(props.type === "day"){
        state.lineList = state.list
      }else{
        state.lineList = mAfterlist
      }

    }

    // 开始时间
    const getStartTime = (type) => {
      let startTime = ""
      if(state.endDate === undefined){
        return  moment(state.endDate).format("YYYY-MM-DD")
      }
      let date = new Date(state.selectdate)
      switch (type){
        case 'day':
          startTime = state.selectdate
          break
        case 'week':
          startTime = date.setDate(date.getDate() - 7)
          break
        case 'month':
          startTime = date.setMonth(date.getMonth() - 1);
          break
        case 'year':
          startTime = date.setFullYear(date.getFullYear() - 1);
          break
      }
      return  moment(startTime).format("YYYY-MM-DD")
    }


    const handleData = (arr) => {
      if(arr.length === 0) return {
        max: 0,
        min: 0,
        average: 0
      }
      let heightMax = Math.max.apply(null,arr)
      let heightMin = Math.min.apply(null,arr);
      let heightAverage = arr.reduce((num, item, index) => {
        if(index != arr.length-1) {
          return num+item;
        } else {
          return (num+item)/arr.length;
        }
      },0);
      return {
        max: heightMax,
        min: heightMin,
        average: heightAverage.toFixed(2)
      }
    }

    // 整合年数据
    const handleYearDatas = (list) => {
      const afterData = [] // 数组
      const beforeData = list// 数组
      let tempArr = [];
      for (let i = 0; i < beforeData.length; i++) {
        if (tempArr.indexOf(beforeData[i].yearDate) === -1) {
          afterData.push({
            yearDate: beforeData[i].yearDate,
            origin: [beforeData[i]]
          });
          tempArr.push(beforeData[i].yearDate);
        } else {
          for (let j = 0; j < afterData.length; j++) {
            if (afterData[j].yearDate == beforeData[i].yearDate) {
              afterData[j].origin.push(beforeData[i]);
              break;
            }
          }
        }
      }
      console.log('年数据处理------', afterData)
      return afterData
    }

    /**
     * 跳入年详情月列表界面
     */
    const toYearDetial = (item) => {
      const json =  JSON.stringify(item.origin)
      console.log("json",json)
      router.push({ path:'/heartrate-yeardetial-list', query: { listJson: json, date: item.yearDate}})
    }

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      heartRate:{
        min: '一一',
        max: '一一',
        average: '一一',
      },
      endDate: undefined,
      currentRate: 0,
      lineList:[],
      todo: '',
      sumCount: 1200,
      selectdate: new Date(),
      list: [],
      baseInfoList:[], // 将每日数据拆开成每次测量集合
      title: '本周数据',
      checked: false, // 是否开启图形分析
      measurePeriodOneObj:{
        max:'一一',
        min:'一一',
        average:'一一',
      },
      measurePeriodTwoObj:{
        max:'一一',
        min:'一一',
        average:'一一',
      },
      measurePeriodThreeObj:{
        max:'一一',
        min:'一一',
        average:'一一',
      },
      measurePeriodFourObj:{
        max:'一一',
        min:'一一',
        average:'一一',
      }
    })


    // 图形分析开关
    const onUpdateValue = (newValue) => {
      state.checked = newValue;
    };

    const onchange=(data) =>{
      state.selectdate = data
      state.endDate = data
      getList()
    }
    return {
      ...toRefs(state),
      onchange,
      onUpdateValue,
      toYearDetial
    }
  }
}
</script>

